<template>
    <div>
        <table class="table table-hover table-dark rounded haven-table" v-if="showHavenReward">
            <thead>
                <tr>
                    <th scope="col" style="width: 25%;">
                        <span>{{ text.hero }}</span>
                    </th>
                    <th scope="col" colspan="2">
                        <span>{{ text.drop }}&nbsp;1</span>
                    </th>
                    <th scope="col" colspan="2">
                        <span>{{ text.drop }}&nbsp;2</span>
                    </th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="item in tableData" :key="item">
                    <th scope="row" class="align-middle">{{ item.name }}</th>
                    <td>
                        <img :src="`img/EquipmentItem/${item.dropItem1}`" 
                        :alt="item.dropItem1">
                    </td>
                    <td class="align-middle">{{ item.dropRate1 }}%</td>
                    <td>
                        <img :src="`img/EquipmentItem/${item.dropItem2}`" 
                        :alt="item.dropItem2">
                    </td>
                    <td class="align-middle">{{ item.dropRate2 }}%</td>
                </tr>
            </tbody>
        </table>
        <div class="d-flex justify-content-center" style="height:50vh;" v-else>
            <div class="spinner-border my-auto" role="status">
                <span class="visually-hidden">Loading...</span>
            </div>
        </div>
    </div>
</template>

<script>
/* eslint-disable */
import { ref, watch } from 'vue'
import { useInitUnit } from '@/store/InitUnit'
import { useLanguage } from '@/store/Language'
import { storeToRefs } from 'pinia'

export default {
    name: 'HavenReward',
    setup() {
        const { language, text } = storeToRefs(useLanguage())

        const showHavenReward = ref(false)
        setTimeout(() => {
            useLanguage().changeLanguage()
            useInitUnit().setLanguage()
            showHavenReward.value = true
        }, "1500")

        useInitUnit().setHavenReward()
        const { units } = storeToRefs(useInitUnit())
        const tableData = ref(units.value)
        
        console.log(tableData)
        return {
            tableData,
            showHavenReward,
            text
        }
    }
}
</script>

<style scoped>
table {
    table-layout:fixed;
    text-align: center;
}
img {
    width: 100%;
}
@media screen and (max-width: 768px) {
    .haven-table>thead>tr>th {
        font-size: xx-small;
    }
}
</style>
