<template>
    <div class="container">
        <HavenReward/>
    </div>
</template>

<script>
/* eslint-disable */
import { ref, watch } from 'vue'
import { useInitUnit } from '@/store/InitUnit'
import { useLanguage } from '@/store/Language'
import { storeToRefs } from 'pinia'
import HavenReward from '@/components/HavenReward.vue'

export default {
    name: 'HavenView',
    components: {
        HavenReward,
    },
    setup() {
        const { language } = storeToRefs(useLanguage())
        
        watch(language, () => {
            useLanguage().changeLanguage()
            useInitUnit().setLanguage()
        })
    }
}
</script>

